import GlobalConf from '../conf/EnigmaConf'
import CryptoJS from 'crypto-js'




const Util = {
    AesKey: "AECF20150822DABE", //
    AesIv: "ADBC20170217EFBD", //

    getAdaptNumber: (num) => {
        let t = num;
        if (t > GlobalConf.LetterCount) {
            let ti = t % GlobalConf.LetterCount;
            t = ti !== 0 ? ti : GlobalConf.LetterCount;
        }
        return t;
    },

    AES: {
        // 
        encrypt: function (key, iv, data) {
            if (typeof data === 'object') {
                // datajsonjson
                try {
                    data = JSON.stringify(data);
                } catch (error) {
                    console.log('error:', error);
                }
            }
            // UTF8
            const dataHex = CryptoJS.enc.Utf8.parse(data); // 
            const keyHex = CryptoJS.enc.Utf8.parse(key); // 
            const ivHex = CryptoJS.enc.Utf8.parse(iv); // 
            const encrypted = CryptoJS.AES.encrypt(dataHex, keyHex, {
                iv: ivHex,
                mode: CryptoJS.mode.CBC, // 
                padding: CryptoJS.pad.Pkcs7,
            });
            let encryptedVal = encrypted.ciphertext.toString();
            return encryptedVal; //  
        },
        // 
        decrypt: function (key, iv, encryptedVal) {
            // keyivkey
            // UTF8
            let encryptedHexStr = CryptoJS.enc.Hex.parse(encryptedVal);
            let srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr);
            const keyHex = CryptoJS.enc.Utf8.parse(key); // 
            const ivHex = CryptoJS.enc.Utf8.parse(iv); // 
            let decrypt = CryptoJS.AES.decrypt(srcs, keyHex, {
                iv: ivHex,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.Pkcs7,
            });
            let decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
            return decryptedStr.toString();
        },
    }
}

export default Util;